.youtube{
    background: #efefef;

    figure{
        width: 100%;
        height: 75vh;
        position: relative; 
        margin-top: -100px;
        overflow: hidden;

        // video{
        //     width:100%;
        //     height:100%;
        //     object-fit: cover;
        // }

        >img{
            width:100%;
            height:100%;
            object-fit: cover;
        }

        >div{
            width: 1180px;
            position: absolute;
            height: 100%;
            left: 50%;
            top: 0;
            transform: translateX(-50%);

            h1{
                position: relative;
                top: 45%;
                left: 50%;
                transform: translate(-50%, -50%);
                font:normal 45px 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 2.3px;
    
                span{
                    display: block;
                    padding-top: 20px;
                    padding-bottom: 20px;
                }
    
                b{
                    color:#fff;
                }
            } 
    
            p{
                position: relative;
                top: 7.5%;
                left: 90%;
                transform: translate(-50%, -50%);
                font:normal 10px/13px 'Orbitron';
                color:#ddd;
                letter-spacing: 1.3px;
                margin-bottom: 30px;

                &:nth-of-type(2){
                    top:12.5%;
                    margin-bottom: 0px;
                } 
            }
        }

        .subvideo{
            position: relative;
            width: 1180px;
            height: 180px;
            top: -200px;

            .swiper{
                width: 100%;
                height: 100%;
                

                .swiper-button-next{
                    color:rgba(255,255,255,.6);
                    transition: 1s;

                    &::after{
                        content: '＞';
                    }

                    &:hover{
                        color:#fff;
                    }
                }

                .swiper-button-prev{
                    color:rgba(255,255,255,.6);
                    transition: 1s;

                    &::after{
                        content: '＜';
                    }

                    &:hover{
                        color:#fff;
                    }
                }

                >.swiper-wrapper{
                    margin-left: -425px;

                    .swiper-slide{
                        width: 100%;
                        height: 100%;
    
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                }


            }
        }
    }
    

    
    .inner{
        h1{}

        >section{
            padding-bottom: 100px;

            &::after{
                content: '';
                display: block;
                clear: both;
            }

            article{
                width: 20%;
                height: 500px;
                float: left;
                padding:10px;
                overflow: hidden;
                margin-bottom: 20px;
                
                .inner{
                    width: 100%;
                    height: 100%;
                    background: #fff;
                    box-shadow: 3px 3px 10px rgba(0,0,0,.1);
                    padding:10px;
                    display: flex;
                    flex-wrap: wrap;
                    align-content: space-between;
                    justify-content: center;

                    /*
                    word-break : 텍스트의 줄바꿈 속성 지정
                    - normal : 한글은 한글자 기준, 영어는 단어를 기준으로 줄바꿈 됨(default)
                    - break-all : 한글과 영어 모두 글자 단위로 줄바꿈
                    - keep-all : 한글, 영어 모두 단어 기준으로 줄바꿈
                    */

                    .txt{

                        h2{
                            font:bold 16px/1.2 'arial';
                            margin-bottom: 20px;
                            color:#555;
                        }

                        p{
                            font:12px/1.2 'arial';
                            color:#888;
                        }
                    }


                    .pic{
                        width: 100%;
                        height: 150px;
                        cursor: pointer;
                        overflow: hidden;

                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            transform: scale(1.6);
                        }
                    }

                }
            }
        }
    }
}

.pop{
    width: 100%;
    height: 90%;
    background: #000;
    position: fixed;
    top: 5%;
    left: 0;
    padding:50px;

    iframe{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    span{
        position: absolute;
        color:#fff;
        font:bold 13px/1 'arial';
        top: 20px;
        right: 20px;
        cursor: pointer;
    }
}