@import url('https://fonts.googleapis.com/css2?family=Orbitron&family=Playfair+Display:ital@0;1&display=swap');

.gallery {

    figure{
      width: 100%;
      height: 75vh;
      position: relative; 
      margin-top: -100px;

      video{
          width:100%;
          height:100%;
          object-fit: cover;
      }
      
      h1{
          position: absolute;
          top: 45%;
          left: 50%;
          transform: translate(-50%, -50%);
          font:bold 30px/1 'Orbitron';
          color:#fff;
          letter-spacing: 2.3px;
      }

      p{
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font:normal 18px/1 'Orbitron';
          color:#ddd;
          letter-spacing: 1.3px;
      }

      span{
          position: absolute;
          top: 55%;
          left: 50%;
          transform: translate(-50%, -50%);
          font:normal 14px/1 'Orbitron';
          color:#ddd;
          letter-spacing: 1.3px;
      }

      b{
          position: absolute;
          top: 95%;
          left: 50%;
          transform: translate(-50%, -50%);
          font:bold 14px/1 'Orbitron';
          color:#eee;
          letter-spacing: 1.7px;
      }
  }

  .gallerySlider{
    position: relative;
    margin: 0 auto;
    width: 1180px;
    height: 800px;
    overflow: hidden;
    margin-bottom: 50px;
    background: #fafafa;
    margin-top: 100px;
    box-shadow: 5px 5px 10px rgba(0,0,0,.3);

    ul{
        position: absolute;
        top: 0;
        left: 0;
        width: calc(100% * 5);
        height: 100%;

        li{
            width: calc(100% / 5);
            height: 100%;
            float: left;
            display: flex;
            justify-content: center;
            align-items: center;
            padding:50px 100px;

            img{
                width: 50%;
                height: 90%;
                object-fit: cover;
            }
            
            .contents{
                width: 50%;
                height: 90%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;
                text-align: center;

                h3{
                    font:bold 16px/1 'Orbitron';
                    color:#111;
                    letter-spacing: 2px;
                    margin-bottom: 30px;
                    width: 80%;
                }

                p{
                    font:normal 11px/15px 'Orbitron';
                    color:#333;
                    letter-spacing: 1.3px;
                    margin-bottom: 15px;
                    width: 80%;
                }
                span{
                    font:bold 10px/1 'Orbitron';
                    color:#333;
                    letter-spacing: 1.1px;
                    margin-bottom: 30px;
                }

                .pages{
                    display: flex;
                    flex-direction: column;
                    width: 20%;
                    
                    b{
                        font:bold 10px/1 'Orbitron';
                        color:#333;
                        letter-spacing: 1.1px;


                        &:nth-child(1){
                            margin-bottom: 10px;
                            text-align: left;
                        }

                        &:nth-child(3){
                            text-align: right;
                        }
                    }

                    >div{
                        width: 100%;
                        height: 2px;
                        background: #000;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }

    .prev{
        position: absolute;
        top: 50%;
        left: 45px;
        transform: translateY(-50%);
        font:bold 15px/1 'Orbitron';
        color:#333;
        cursor: pointer;
        border:none;
        background: none;
    }

    .next{
        position: absolute;
        top: 50%;
        right: 45px;
        transform: translateY(-50%);
        font:bold 15px/1 'Orbitron';
        color:#333;
        cursor: pointer;
        border:none;
        background: none;
    }
  }

    .innerWrap {
      transform: translateY(200px);
      opacity: 0;
      transition: 1s;

      >.inner {
        transition: none;
        width: 1180px;
        margin: 0px auto;
        min-height:  500px;
        padding-top: 100px;

        >h2{
          font:bold 30px/1 'Orbitron';
          color:#111;
          letter-spacing: 5px;
          margin-bottom: 10px;
          text-align: center;
        }

        >span{
          font:normal 15px/1 'Orbitron';
          color:#555;
          letter-spacing: 2.5px;
          text-align: center;
          display: block;
          margin-bottom: 30px;
        }

        .searchBox{
          position: absolute;
          right: 400px;
        }

        .loading {
          position: absolute;
          top:0px;
          left: 50%;
          width: 50%;
          transform: translateX(-50%);
        }

        >section {
          transform: translateY(200px);
          opacity: 0;
          transition: 1s;
          margin-top: 75px;
          margin-bottom:50px;
    
          &.on {
            transform: translateY(0px);
            opacity: 1;
          }
    
          &::after {
            content: '';
            display: block;
            clear: both;
          }
    
          article {
            width: 20%;
            float: left;
            padding: 10px;
    
            >.inner {
              width: 100%;
              height: 100%;
              background: #fff;
              box-shadow: 5px 5px 10px rgba(0,0,0, .1);
              padding: 10px;
    
              .pic {
                width: 100%;
                margin-bottom: 10px;
                cursor: pointer;
    
                img {
                  width: 100%; 
                }            
              }
    
              >b {
                font:normal 10px/1 'Orbitron';
                color:#555;
                letter-spacing: 2.5px;
                margin-bottom: 20px;
              }
            }
          }
        }
      } 
    } 
    
    &.on {
      .innerWrap {
        transform: translateY(0px); opacity: 1;
      }
    }
  }
  
  .popup {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.9);
    position: fixed;
    top: 0px;
    left: 0px;
    padding: 5vw;
    z-index: 6;
  
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  
    span {
      font: bold 14px/1 'arial';
      color: #fff;
      position: absolute;
      top: 4vh;
      right: 5vw; cursor: pointer;
    }
  }
  
  @media screen and (max-width: $tablet){
    .content {
      >figure {
        height: 200px;
      }
  
      .innerWrap {
        >.inner {
          width: 100%;
          padding: 0px 3vw;
          padding-top: 80px
        }
      }
      
    }
  }
  
  @media screen and (max-width: 1179px){
    .gallery {
      .item {width: 25%!important;}
    }
  }
  
  @media screen and (max-width: 899px){
    .gallery {
      .item {width: 33.333%!important;}
    }
  }
  @media screen and (max-width: 739px){
    .gallery {
      .item {width: 50%!important;}
    }
  }
  @media screen and (max-width: 479px){
    .gallery {
      .item {width: 100%!important;}
    }
  }
 